body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bntWhatsFixo{
    height: 60px;
    position: fixed;
    bottom: 25px;
    right: 25px;
    z-index: 2;
 }
 
 @media screen and (max-width:780px){
    .bntWhatsFixo{
       height: 50px;
       right: 15px;
    }
 }
#btMenu{
    display: none;
}

.navResposinvo{
    display: none;
}

.logo{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 1%;
    width: 10%;
}

.simbolLogo{
    width:120px;
    height:48px;
    margin: 3px;
    cursor: pointer;
}

.sublimeLogo{
    width: 80%;
    height: 10px;
    margin: 3px;
    cursor: pointer;
}

.nav{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
}

.menuHamburguer{
    display: none;
}

@media screen and (max-width: 752px){
    .logo{
        width: 15%;
    }

    .nav{
        width: 85%;
    }
}

@media screen and (max-width: 500px){
    .logo{
        width: 50%;
    }

    .nav{
        justify-content: flex-end;
        padding-right: 20px;
    }

    .menuHamburguer{
        display: flex;
        width: 30%;
        color: #6e717a;
    }

    .navResposinvo{
        display: flex;
        font-family: 'Montserrat' sans-serif;
        font-weight: bold;
        color:#fff;
        font-size: 14px;
        position: absolute;
        left: -100%;
        top: 60px;
        transition: all .7s;
        flex-direction: column;
        background-color: #6e717a;
        width: 100%;
        margin:0%;
        padding: 0%;
        padding-bottom: 2%;
        padding-top: 2%;
    }

    #btMenu:checked ~ .navResposinvo{
        position: absolute;
        left:0%;
        top: 60px;

    }

    .linkNav{
        text-decoration: none;
        color: inherit;
    }
    
    .labelName{
        
        font-size: 1.7em;
    }
}
.headerContainer{
    display: flex;
    position: fixed;
    top: 0em;
    background-color: #f5f5f5;
    width: 100%;
    opacity: 90%;
    height:auto;
    z-index: 3;
}

@media screen and (max-width: 500px){
    .headerContainer{
        justify-content: space-between;
    }
}
.buttonHeader{
    text-decoration: none;
    font-family: 'Montserrat' sans-serif;
    font-weight: bolder;
    color:#122888;
    font-size: 18px;
    cursor: pointer;
    margin: 1%;
    width: 15%;
    padding: 15px;
}

.buttonHeader:hover{
    border-radius: 5px;
    color:#f1f3f5;
    background-color:#122888;
}

@media screen and (max-width: 752px){
    .buttonHeader{
        width: 25%;
        margin: 0;
    }
}

@media screen and (max-width: 500px){
    .buttonHeader{
        display: none;
    }
}
.buttonDefault{
    text-decoration: none;
    width: 15%;
    border: solid 1px #ffff;
    color:#5165a8;
    font-size: 18px;
    background-color: transparent;
    cursor: pointer;
    padding: 10px;
}

.buttonDefault:hover{
    background-color: rgba(30,30,30);
    opacity: 70%;
    border: solid 0px #ffff;
    color: #f5f5f5;
}

@media screen and (max-width: 768px){
    .buttonDefault{
        width: 30%;
    }
}

@media screen and (max-width: 500px){
    .buttonDefault{
        width: 50%;
    }
}
.homeImage{
    font-family: 'Roboto', sans-serif;
    color: #fff;
    text-shadow: 1px 1px 0px rgb(5, 5, 5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-image: url(/static/media/homeImage.d1103d10.jpg);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-top: 300px;
    padding-bottom: 400px;
}

.titleImg{
    font-size: 3em;
}

.pImg{
    margin-top: 0px;
    margin-bottom: 50px;
    font-size: 1.5em;
}

.buttonParallax:hover{
    color:#c69f36;
    background-color: #4d4d4d;
}

@media screen and (max-width: 500px){
    .homeImage{
        padding-bottom: 300px;
    }

    .titleImg{
        font-size: 2em;
    }

    .pImg{
        margin-top: 0;
        font-size: 1em;
    }
}
.home{
    display: flex;
    width: 100%;
    background-image: url(/static/media/agua.bacaf962.png);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-top: 10px;
    padding-bottom: 50px;
}

.homeDestaq{
    font-size: 70px;
}

.bannerHome{
    border-radius: 10px;
    width: 60%;
}

.contentHome{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 90px;
    padding: 15px;
    width: 50%;
    font-family: 'Poppins', sans-serif;
    color: #fff;
    text-shadow: 1px 1px 0px rgb(95, 95, 95);
    font-size: 1.3em;
}

@media screen and (max-width: 768px){
    .bannerHome{
        margin-top: 80px;
        width: 100%;
    }

    .homeDestaq{
        font-size: 40px;
    }
    .contentHome{
        margin-top: 0;
        width: 90%;
    }

    .home{
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
    }
}



@media screen and (max-width: 500px){
    .bannerHome{
        margin-top: 80px;
        width: 100%;
    }

    .contentHome{
        margin-top: 0;
        width: 90%;
    }

    .homeDestaq{
        font-size: 30px;
    }

    .home{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
