    padding-top: 300px;
    padding-bottom: 300px;
}

@media screen and (max-width: 768px){
    .service{
        padding-top: 200px;
        padding-bottom: 200px;
    }
}
.servico{
    font-family: 'Poppins', sans-serif;
    color: #122888;
    display: flex;
    flex-direction: column;
    justify-content:  space-around;
    flex-wrap: nowrap;
    width: 100%;
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding-top: 50px;
    padding-bottom: 50px;
}

.content{
    display: flex;
}

.contentServico{
    border-radius: 10px;
    background-color: #fff;
    width: 49%;
}

.contentServico h1{
    font-size: 1em;
    padding-right: 20px;
    padding-left: 20px;
}

.contentServico p{
    padding-right: 20px;
    padding-left: 20px;
}

@media screen and (max-width: 750px){
    .servico{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-bottom: 200px;
    }

    .content{
        flex-direction: column;
        justify-items: center;
        align-items: center;
    }

    .contentServico{
        border-radius: 0px;
        background-color: #fff;
        width: 90%;
    }
}
#orcamento{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #1330d3;
    padding-bottom: 50px;
    padding-top: 80px;
    background-color: rgb(228, 228, 241);
}

.contentBudget{
    width: 100%;
    text-align: end;
    margin-bottom: 10px;
}
.labelBudget{
    padding: 50px;
}

.inputBudget{
    margin-right: 18%;
    width: 60%;
    font-size: x-large;
}

.buttonBudget{
    border: 0.4px solid ;
    cursor: pointer;
    font-size: large;
    border-radius: 5px;
    color: #1330d3;
    width: 15%;
    height: 35px;
}

.buttonBudget:hover{
    color: #f5f5f5;
    background-color: #1330d3;
}

@media screen and (max-width: 768px){
    #orcamento{
        padding-bottom: 50px;
        padding-top: 50px;
        justify-content: center;
        align-items: center;
    }

    .contentBudget{
        width: 90%;
        text-align: start;
        justify-content: center;
        margin-bottom: 10px;
    }
    .labelBudget{
        padding: 5px;
    }
    
    .inputBudget{
        width: 100%;
        font-size: large;
        margin-right: 0%;
    }

    .buttonBudget{
        border-radius: 5px;
        color: #1330d3;
        width: 50%;
        height: 30px;
    }
}


